@import url(https://fonts.googleapis.com/css?family=Raleway:400,700&display=swap);
html {
  font-family: Raleway, sans-serif; }

body {
  background: url(/react_people-table/static/media/light-gray.c8c3420f.jpg);
  margin: 0; }

.App {
  padding: 40px; }

.main-title {
  margin: 0; }

.table-info {
  font-weight: bold; }

.table-search {
  margin-bottom: 15px; }

.people-table {
  border-collapse: collapse; }
  .people-table th {
    background-color: #f58585; }
  .people-table .sort-button {
    border: none;
    width: 100%;
    background-color: #f58585;
    cursor: pointer;
    outline: none; }
  .people-table th,
  .people-table td {
    border: 1px solid black;
    padding: 0 10px;
    line-height: 1.5; }

.person--male {
  background-color: #c5e0de; }

.person--female {
  background-color: #ddddc2; }

.person--born-before-1650 {
  text-decoration: line-through; }

.person--age-over-65 {
  color: green; }

.person--selected {
  border: 1px solid red;
  background-color: red; }

